*, :after, :before {
  box-sizing: border-box;
}

body {
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
}

.container {
  min-height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

.chat {
