  text-align: right;
  background-color: #f7f7f7;
  align-self: end;
}

.partners-message {
  background-color: #e5e5e5;
  align-self: start;
}

.message {
  max-width: 288px;
  margin-bottom: 8px;
  padding: 8px;
}

.message__time {
  font-size: 14px;
  font-style: italic;
  font-weight: 300;
  line-height: 16px;
}

.my-message.message__time {
