}

.message__text {
  margin-bottom: 4px;
}

.chat-form {
  justify-content: space-between;
  display: flex;
}

.chat-form__input {
  border: 1px solid #000;
  flex-grow: 1;
  margin-right: 8px;
  padding: 7px;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
}

