  color: #7c7c7c;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
}

.chat-form__button {
  cursor: pointer;
  background-color: #0000;
  border: 1px solid #000;
  padding: 7px;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
}

.modal-overlay {
  height: 100vh;
  width: 100vw;
  background-color: #545353cc;
  justify-content: center;
  align-items: center;
  display: none;
  position: absolute;
  top: 0;
  left: 0;
}

.modal-overlay--open {
  display: flex;
}

.modal {
  width: 273px;
  height: 319px;
  background-color: #fff;
  padding: 16px 14px;
}

.modal__top {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 70px;
  display: flex;
}

.modal__close {
  width: 10px;
  height: 11px;
  cursor: pointer;
  background-color: #0000;
  background-image: url("close-icon.6fd5d052.svg");
  background-repeat: no-repeat;
  border: none;
}

.modal__text {
